import React,{useState, useEffect} from "react"

type InputType = "phone" | "name" | "password" | "email" | "adress"
export const useValidate = (prop: InputType, value: string): { error: string, errorStatus: boolean  } => {

  const [error, setError] = useState<string>("")
  const [errorStatus, setErrorStatus] = useState<boolean>(true)

  useEffect(() => {
    const number: number = value.length

    switch (prop) {
      case "email":
        const reger = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        if (value.length < 1) {
          setError("email не должен быть пустым")
          setErrorStatus(true)
        }
        else if (!reger.test(value)){
          setError("не корректный email")
          setErrorStatus(true)
        }
        else setErrorStatus(false)
        break

      case "password":
        
        if (number < 1) setError("password won't to be an empty")
        else if (number < 5) setError("your password is very short")
        else if (number > 11) setError("your password is very long")
        else if (value.includes(" ")) setError("your password has whitespace")
        else if (!/[0-9]/.test(value)) setError("your password doesn't have any digit")
        else if (/^[a-zа-я\d]*$/.test(value)) setError("your password doesn't have contain any capital letter")
        else setError("")
        break
      
      case "name":
        if (number == 0) {
          setError("Вы не указали имя")
          setErrorStatus(true)
        }
        else if (/[0-9]/.test(value)) { 
          setError("В имене не должно быть цифр")
          setErrorStatus(true)
        }
        else if (number < 2) {
          setError("Ваше имя слишком короткое")
          setErrorStatus(true)
        }
        else if (number > 10) {
          setError("Ваше имя слишком длинное")
          setErrorStatus(true)
        }
        else setErrorStatus(false)
        break
      
      case "phone":
        if (number === 0) {
          setError("Вы не указали номер телефона")
          setErrorStatus(true)
        }
        else if (number < 11) {
          setError("Номер слишком короткий")
          setErrorStatus(true)
        }
        else if (number > 11) {
          setError("Номер слишком длинный")
          setErrorStatus(true)
        }
        else if (value[0] != "8" || value[1] != "9") {
          setError("Не корректный номер телефона")
          setErrorStatus(true)
        }
        else {
          setErrorStatus(false)
        }
          
        break
      
      case "adress": 
        if (!/[а-яё]/i.test(value)) {
          setError("Адресс должен быть указан на кирилице")
          setErrorStatus(true)
        }
        else { setErrorStatus(false) }
        break
        
    }
  }, [value])

  return {error, errorStatus}
}