import React from 'react';
// @ts-ignore
import { ReactComponent as PizzaShop } from '../../assets/img/pizza_shop.svg';

function Footer() {
    return (
      <footer className="wall-paper">
        <div className="footer">
          <div className="container">
            <div className="footer__logo">
              <PizzaShop className="footer__picture" />
              <p>Тестовая работа Ильи Мишкова,<span>в данной работе применялись следующие технологии</span> </p>
            </div>

            <div className="footer__info">
              <Colomn title={'Языки'} subTitles={['TypeScript', 'JavaScript']} />
              <Colomn title={'Библиотеки'} subTitles={['React JS', 'Redux toolkit']} />
              <Colomn title={`Библиотеки поменьше`} subTitles={['react-router-dom', "react-transition-group", "axiox", "lodash"]} />
              <Colomn title={'Дополнительно'} subTitles={['Redux asyncThunk', "SCSS"]} />
              <Colomn title={'Сервер'} subTitles={['mockAPI']} />
            </div>

            <div className='footer__contacts'>
              контакты для сотрудничества <br />
              ilya.mishkov@gmail.com
              <br />
              8-977-439-34-18
              
            </div>
              
          </div>
        </div>
        
      </footer>
    );
}



function Colomn({ title, subTitles }: { title: string; subTitles: string[] }) {
  return (
    <div className="footer__column">
      <Link
        text={title}
        className="footer__title"
      />
      <div className='footer__subTitle-container'>
        {subTitles.map((subTitle, index) => (
          <Link
            key={index}
            text={subTitle}
            className="footer__subtitle"
          />
        ))}
      </div>
      
    </div>
  );
}

function Link({ text, className }: { text: string, className: string }) {
  return (
    <a href="#">
      <p className={className} >{text}</p>
    </a>
  )
}


export default Footer;